.footer
{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    position: relative;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    max-height: 20vh;
}

.main
{
    font-size: 16px;

    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;

    width: 100%;
    height: 100%;

    box-shadow: 0 0 13px #cccccc;
  /*border-color: rgba(55, 55, 55, 0.78);*/
  background-color: #000000;
   /*opacity: 0.2;*/
}

.title
{
    font-size: 14px;
color: cyan;
    margin: 15px;
letter-spacing: 2px;
}

.wrapper {
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: space-evenly;

    width: 360px;
    height: 100%;
    /*margin: 10px;*/
}

.icon
{
    display: flex;
    align-items: center;
    justify-content: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 50%;
    transition: 1000ms;
    width: 50px;
    height: 50px;
}
.icon:hover{
    transform: scale(1.1, 1.1);
    transition: 1s;
}

@media screen and (max-width: 500px)
{
    .wrapper
    {
        width: 100%;
    }
}
@media screen and (max-width: 500px)
{
    .icon
    {
        width: 50px;
        height: 50px;
    }
}
